<template>
  <div id="networking-map"></div>
</template>

<script>
    export default {
        name: "networkingMap",
        data(){
          return{

          }
        },
        methods:{
          /* 创建并初始化地图 */
          createNetworkingMap(){
            let myChart =  this.$echarts.init(document.getElementById('networking-map'));
            let option = {};
            myChart.setOption(option);
          },
        },
        mounted(){
          this.createNetworkingMap();
        },
        created(){

        }
    }
</script>

<style scoped>
#networking-map{
  height: 100%;
  width: 100%;
  background: url("../../assets/images/acquisition/map-bg.png") no-repeat center center;
}
</style>



